export default {
  SET_EMPLOYEES(state, employees) {
    state.employees = employees.data
  },
  REMOVE_RECORD(state, itemId) {
      const employeeIndex = state.employees.findIndex((u) => u.id == itemId)
      state.employees.splice(employeeIndex, 1)
  },
}
